class PhoneStates {
    constructor(phone) {

        this.phone = phone
        this.InitialState = "NotInit"
        this.state = ""
        this.subStatuses = {
            HangupRequest: {
                subStatus: "HangupRequest",
                change: () => {
                    var self = this
                    console.log(`got HangupRequest change`)
                    self.phone.sip.hangup()
                        .catch((io) => {
                            console.Error("hangup error: ", io)
                            this.phone.changeState("Error")
                        })
                }
            },
        }
        this.states = {
            NotInit: {
                state: "NotInit",
                allowedStates: ["Initilazing", "Close"]
            },
            Initilazing: {
                state: "Initilazing",
                allowedStates: ["Dialing", "Hangup", "CancelDialing", "Error", "ErrorMic"],
                onEnter: () => {
                    console.log("Init ", this)
                    this.phone.sip.initPhone()
                        .then(
                            () => {
                                this.phone.changeState("Dialing")
                            }
                        ).catch(
                            (io) => {
                                console.error("Error", io) //TODO what do we need to do in case of an error?
                                this.phone.changeState("Error")
                            });
                },
                subStatuses: { HangupRequest: this.subStatuses.HangupRequest }

            },
            Dialing: {
                state: "Dialing",
                allowedStates: ["Call", "Hangup", "CancelDialing", "Error", "ErrorMic"],
                onEnter: () => {

                    console.log("Dialing ", this)
                    // setTimeout(() => { self.changeState("Call") }, 2000)
                    this.phone.sip.call(this.phone.did)
                },
                subStatuses: { HangupRequest: this.subStatuses.HangupRequest }
            },
            Call: {
                state: "Call",
                allowedStates: ["Hangup", "Error", "ErrorMic"],
                onEnter: () => {
                    console.log("Call ", this)
                },
                subStatuses: {
                    digitPressState: {
                        subStatus: "digitPressState",
                        change: (digit) => {
                            var self = this

                            console.log(`got digitPressState change, current ${this}, change to ${digit}`)
                            self.phone.sip.sendDTMF(digit)
                                .then(() => {
                                    self.phone.updateSubState("digitPressState", digit)

                                }).catch(() => {
                                    console.error("key was not recieved") //TODO what do we need to do in case of an error?
                                })
                            // self.updateSubState("digitPressState", digit)

                        }
                    },
                    muteState: {
                        subStatus: "muteState",
                        change: (state) => {
                            var self = this
                            console.log(`got muteState change, current ${this.subStatus}, change to ${state}`)
                            var mute = self.phone.sip.mute()
                            this.phone.updateSubState("muteState", mute)
                        }
                    },
                    HangupRequest: this.subStatuses.HangupRequest
                }
            },
            CancelDialing: {
                state: "CancelDialing",
                allowedStates: ["Hangup"],
                onEnter: () => {
                    var self = this
                    this.phone.sip.hangup()
                        .catch((io) => {
                            console.Error("hangup error: ", io)
                            self.phone.changeState("Error")
                        })
                }
            },
            Hangup: {
                state: "Hangup",
                allowedStates: ["Reset", "Close", "Error", "ErrorMic"],
                onEnter: (privState) => {
                    if (privState.state == "Dialing") {
                        throw "dialing not accepted"
                    }
                }
            },
            Error: {
                state: "Error",
                allowedStates: ["RedialPage", "Error", "ErrorMic"],
            },
            ErrorMic: {
                state: "ErrorMic",
                allowedStates: ["RedialPage", "Error", "ErrorMic"],
            },
            RedialPage: {
                state: "RedialPage",
                allowedStates: ["Reset", "Close"],
            },
            Reset: {
                state: "Reset",
                allowedStates: ["Initilazing"],
                onEnter: () => {

                    this.changeState("Initilazing")
                }
            },
            Close: {
                state: "Close",
                allowedStates: [],
                onEnter: () => {
                    //Init the phone here
                    console.log("bye bye ;-)")
                    window.close()
                }
            },
        }
        this.changeState(this.InitialState)
    }
    changeState(ns) {
        var newState = this.states[ns]
        if (newState == undefined) {
            console.log(`changeState state ${ns} not found, ignore`)
            return
        }

        const curState = this.states[this.state]
        if (curState == undefined || curState.allowedStates.includes(ns)) {
            //Apply the new state
            console.log(`changeState Changing to ${newState.state} from ${curState ? curState.state : "undefined"}`)
            this.state = ns
            // this.phone.updateState(this.state)
            if (newState.onEnter != undefined) {
                try {
                    newState.onEnter(curState);
                } catch (io) {
                    this.phone.changeState("Error")
                    return
                }
            }
            this.phone.updateState(this.state)

        } else {
            console.log(`changeState currnet state ${curState.state} does not allow change to the new state ${ns} , ignoring`)
            return
        }
    }
    changeSubState(subState, status) {
        var curState = this.states[this.state]
        if (curState == undefined) {
            console.log(`changeSubState curState ${this.state} not found`)
            return
        }
        if ("subStatuses" in curState && subState in curState.subStatuses) {
            var curSubState = curState.subStatuses[subState]
            if ("change" in curSubState)
                curSubState.change(status)
        } else {
            console.log(`changeSubState status ${this.state} does not contain the ${subState} sub status`)
            return
        }
    }
    updateSubState(subState, status) {
        this.phone.updateSubState(subState, status)
    }
}

export default PhoneStates