import BrowserDetect from "./BrowserDetect";
import PhoneStates from "./PhoneStates";
import SIP from "./SIP";

class Phone {
  constructor(vueUpdateState, vueUpdateSubState, remoteAudio, i18n) {
    this.vueUpdateState = vueUpdateState;
    this.vueUpdateSubState = vueUpdateSubState;
    this.remoteAudio = remoteAudio;
    const url = new URL(location.href).searchParams;
    const did = url.get("did");
    if (did && did != "") {
      this.did = did;
    } else {
      console.log(Error); //TODO move to error state
    }

    const cn = url.get("company_name");
    if (cn) {
      this.companyName = cn;
    }
    const lang = url.get("lang");
    if (lang) {
      i18n.locale = lang;
    }
    console.log("local ", i18n.locale);
    const sipRouter = process.env.VUE_APP_SIP_ROUTER;
    if (!sipRouter || sipRouter == "") {
      console.Error("not sip Router configured"); //TODO move to error
    } else {
      this.sipRouter = sipRouter;
    }
    const realm = process.env.VUE_APP_REALM;
    if (realm && realm != "") {
      this.realm = realm;
    } else {
      console.Error("not realm configured"); //TODO move to error
    }
    this.displayName = "Anonymous";
    const customerName = url.get("customer_name");
    if (customerName) {
      this.displayName = customerName;
    }
    console.log("Starting phone ");
    this.sip = new SIP(this); //TODO get displayName
    this.phoneStates = new PhoneStates(this);
    this.middleClass = this.isChromeIOS() ? "middle ios" : "middle";
    this.bottomClass = this.isChromeIOS() ? "bottom ios" : "bottom";
  }

  changeState(state) {
    this.phoneStates.changeState(state);
  }

  updateState(state) {
    this.state = state;
    this.vueUpdateState(this.state);
  }

  changeSubState(subState, status) {
    this.phoneStates.changeSubState(subState, status);
  }

  updateSubState(subState, status) {
    this.vueUpdateSubState(subState, status);
  }

  isChromeIOS() {
    return BrowserDetect.browser().isChromeIOS;
  }
}
export default Phone;
