<template>
  <div id="phone">
    <div class="MainBoard" :style="componentStyles">
      <span style="background-color: red"></span>
      <img
        class="logo"
        src="../assets/img/logo_SOHO03.png"
        srcset="
          ../assets/img/logo_SOHO03.png    1x,
          ../assets/img/logo_SOHO03@2x.png 2x
        "
      />

      <div class="SecendaryBoard">
        <Welcome
          @ChangeState="changeState"
          :companyName="companyName"
          :middleClass = "middleClass"
          v-if="this.state == 'NotInit'"
        />
        <Waiting
          @ChangeState="changeState"
          @ChangeSubState="changeSubState"
          :companyName="companyName"
          :middleClass = "middleClass"
          v-if="this.state == 'Initilazing' || this.state == 'Dialing'"
        />
        <Call
          @ChangeState="changeState"
          @ChangeSubState="changeSubState"
          :companyName="companyName"
          :middleClass = "middleClass"
          :bottomClass = "bottomClass"
          ref="callUpdate"
          v-if="this.state == 'Call'"
        />
        <Hangup
          @ChangeState="changeState"
          :companyName="companyName"
          :middleClass = "middleClass"
          v-if="this.state == 'Hangup'"
        />
        <Error
          @ChangeState="changeState"
          :companyName="companyName"
          :middleClass = "middleClass"
          v-if="this.state == 'Error'"
        />
        <ErrorMic
          @ChangeState="changeState"
          :companyName="companyName"
          :middleClass = "middleClass"
          v-if="this.state == 'ErrorMic'"
        />
        <Redial
          @ChangeState="changeState"
          :companyName="companyName"
          :middleClass = "middleClass"
          v-if="this.state == 'RedialPage'"
        />
        <div class="iOSMessage" :style="iOSVisibility">{{ $t("ios_chrome") }}</div>
      </div>
      
      <audio id="phone_remote_audio" hidden>
        Your browser does not support the audio element.
      </audio>
    </div>
    
  </div>
</template>

<script>
import Welcome from "./Welcome.vue";
import Waiting from "./Waiting.vue";
import Call from "./Call.vue";
import Error from "./Error.vue";
import ErrorMic from "./ErrorMic.vue";
import Hangup from "./Hangup.vue";
import Redial from "./Redial.vue";
import Phone from "../store/Phone";

export default {
  name: "Phone",
  props: {
  },
  components: {
    Welcome,
    Waiting,
    Call,
    Error,
    ErrorMic,
    Hangup,
    Redial,
  },

  data() {
    return {
      state: "",
      did: "",
      lang: "en",
      companyName: "",
      middleClass: "",
      bottomClass: ""
    };
  },
  methods: {
    changeState(state) {
      this.phone.changeState(state);
    },
    updateState(state) {
      this.state = state;
    },
    changeSubState(subState, status) {
      this.phone.changeSubState(subState, status);
    },
    updateSubState(subState, status) {
      this.$refs.callUpdate.updateSubState(subState, status);
    },
    fillDiv(proportional) {
      var currentWidth = this.div.offsetWidth;
      var currentHeight = this.div.offsetHeight;

      var availableHeight = window.innerHeight;
      var availableWidth = window.innerWidth;

      var scaleX = availableWidth / currentWidth;
      var scaleY = availableHeight / currentHeight;
      // console.log(
      //   `fillDevice currentWidth:${currentWidth} currentHeight:${currentHeight} availableHeight:${availableHeight} availableWidth"${availableWidth} `
      // );

      if (proportional) {
        scaleX = Math.min(scaleX, scaleY);
        scaleY = scaleX;
      }

      if (scaleX > 1) {
        scaleX = 1;
      }
      if (scaleY > 1) {
        scaleY = 1;
      }
      this.div.style["-webkit-transform"] =
        "translate(0px, 0px) scale3d(" + scaleX + ", " + scaleY + ", 1)";
      this.div.style.position = "fixed";
      this.div.style.left = "0px";
      this.div.style.top = "0px";
      this.div.style["-webkit-transform-origin"] = "0 0";
    },
    onbeforeunload() {
      if (
        this.state === "Initilazing" ||
        this.state === "Dialing" ||
        this.state === "Call"
      ) {
        return "Want to leave this page? if you close the browser, you voice call will be terminated";
      }
    },

    failover() {
      var self = this;
      window.addEventListener("resize", function () {
        setTimeout(() => {
          self.fillDiv(true);
        }, 500);
      });
    },
    initialize() {
      var self = this;
      this.div = document.getElementById("phone");
      console.log("init");
      this.fillDiv(this.div, true);
      this.failover();

      //window.addEventListener('resize', function() { setTimeout(function() { fillDiv(div, true); }, 500) })

      if ("onorientationchange" in window) {
        console.log("Using orientationchange");
        // There seems to be a bug in some Android variants such that the
        // metrics like innerHeight and outerHeight (used in fillDiv above)
        // are not updated when the orientationEvent is triggered. The
        // half-second delay gives the browser a chance to update the
        // metrics before rescaling the div.
        window.addEventListener("onorientationchange", () => {
          setTimeout(() => {
            self.fillDiv(true);
          }, 500);
        });
      } else if ("ondeviceorientation" in window) {
        //TODO fix
        console.log("Using deviceorientation");
        try {
          window.addEventListener("ondeviceorientation", () => {
            setTimeout(() => {
              self.fillDiv(true);
            }, 500);
          });
        } catch (io) {
          console.log("catch");
          self.failover();
        }

        // Unlike the event above this not limited to a horzontal/vertical
        // flip and will trigger for any device orientation movement
      } else {
        console.log("No orientation supported, fallback to resize");
        window.addEventListener("resize", function () {
          setTimeout(() => {
            self.fillDiv(true);
          }, 500);
        });
      }
    },
  },
  // beforeMount: function () {
  mounted() {
    var ra = document.getElementById("phone_remote_audio");
    this.phone = new Phone(
      this.updateState.bind(this),
      this.updateSubState.bind(this),
      ra,
      this.$i18n
    );
    this.did = this.phone.did;
    this.lang = this.phone.lang;
    this.companyName = this.phone.companyName; // this.phone.companyName;
    this.middleClass = this.phone.middleClass;
    this.bottomClass = this.phone.bottomClass;

    window.onbeforeunload = this.onbeforeunload;
    this.initialize();
    var font = this.$t("Font");
    if (font !== "Font" && font !== "") {
      document.getElementById("phone").style["font-family"] = font;
    }
  },
  computed: {
    isOS: function() {
      return this.middleClass == "middle ios";
    },
    iOSVisibility: function() {
      if (this.isOS) {
        return {
          display: "flex"
        };
      } else {
        return {
          display: "none"
        };
      }
    },
    componentStyles() {
      return {
        height: this.isOS ? `737px` : `730px`
      };
    }
  }
};
</script>